import ScrollToBottom from "react-scroll-to-bottom";
import "./App.css";
import { useEffect, useState } from "react";
import io from 'socket.io-client'
import { MessageByUser } from "./component/message-by-customer";
const socket = io.connect('http://localhost:3030', {
  withCredentials: true,
})

function Chat({ username, messages, enterMessage, setUserMessage, currentMessage, error }) {
  return (
    <div className="chat-window">
      <div className="chat-header">
        <h3>hello {username}</h3>
        {
          error === "" ? (<div></div>) : <p>{error}</p>
        }
      </div>
      <div className="chat-body">
        <ScrollToBottom className="message-container">
          {
            messages.map(msg => {
              return (
                <MessageByUser type={msg.type} message={msg.message} />
              )
            })
          }
        </ScrollToBottom>
      </div>
      <div className="chat-footer">
        <input
          type="text"
          value={currentMessage}
          placeholder="Hey..."
          onChange={setUserMessage}
          onKeyPress={(event) => {
            // event.key === "Enter" && sendMessage();
          }}
        />
        <button onClick={enterMessage}>&#9658;</button>
      </div>
    </div>
  );
}

function App() {
  const [username, setUsername] = useState("");
  const [name, setName] = useState('');
  const [messages, setMessages] = useState("");
  const [currentMessage, setCurrentMessage] = useState("")
  const [error, setError] = useState("")
  const [authenticated, setAuthenticated] = useState(false)
  console.log(authenticated)
  const joinChat = () => {
    socket.emit("authName", { name: username })
  };
  const setUserMessage = e => {
    setCurrentMessage(e.target.value)
  }
  const enterMessage = () => {
    const data = {
      type: "user",
      message: [currentMessage]
    }
    socket.emit("user-message", data)
    // setMessages((list) => [...list, data])
    setCurrentMessage("")
  }
  useEffect(() => {
    socket.on('message', (data) => {
      setMessages(data)
    })
    socket.on('authenticated', data => {
      setAuthenticated(data.authenticated)
      setName(data.name)
      setUsername('')
    })
  }, [])

  return (
    <div className="App">
      {!authenticated ? (
        <div className="joinChatContainer">
          <h3>Welcome to Restaunrantee</h3>
          <p>Please provide name to continue</p>
          <input
            type="text"
            placeholder="John..."
            onChange={(event) => {
              setUsername(event.target.value);
            }}
          />
          <button onClick={joinChat}>Proceed</button>
        </div>
      ) : (
        <Chat
          error={error}
          enterMessage={enterMessage}
          currentMessage={currentMessage}
          setUserMessage={setUserMessage}
          messages={messages}
          username={name}
        />
      )}
    </div>
  );
}

export default App;