import React from 'react';

export const MessageByUser = ({ message, type }) => {
  return (
    <div className="message" id={type === "bot" ? "other" : "you"}>
      <div className={`message-content ${type === "bot" ? "botcolor" : "usercolor"}`}>
        <div>
          {message.map(mg =>
            <p>{mg}</p>
          )}
        </div>
      </div>
    </div>
  );
};
